:root {
  /* Sizes */
  --size-1: 0.4rem;
  --size-2: 0.8rem;
  --size-3: 1.2rem;
  --size-4: 1.6rem;
  --size-5: 2.4rem;
  --size-6: 3.2rem;
  --size-7: 4.8rem;
  --size-8: 6.4rem;
  --size-9: 9.6rem;
  --size-10: 12.8rem;

  /* Colors */
  --color-primary-1: hsl(28, 60%, 95.1%);
  --color-primary-2: hsl(27.6, 60.98%, 83.92%);
  --color-primary-3: hsl(27.86, 60%, 72.55%);
  --color-primary-4: hsl(27.73, 60.41%, 61.37%);
  --color-primary-5: hsl(27.84, 60%, 50%);
  --color-primary-6: hsl(28.24, 60.41%, 38.63%);
  --color-primary-7: hsl(27.86, 60%, 27.45%);
  --color-primary-8: hsl(28.16, 59.04%, 16.27%);
  --color-primary-9: hsl(28, 60%, 4.9%);
  --color-success-1: hsl(120, 38.46%, 94.9%);
  --color-success-2: hsl(120, 38.1%, 83.53%);
  --color-success-3: hsl(120, 38.57%, 72.55%);
  --color-success-4: hsl(120, 38.38%, 61.18%);
  --color-success-5: hsl(120, 38.82%, 50%);
  --color-success-6: hsl(120, 38.38%, 38.82%);
  --color-success-7: hsl(120, 38.57%, 27.45%);
  --color-success-8: hsl(120, 38.1%, 16.47%);
  --color-success-9: hsl(120, 38.46%, 5.1%);
  --color-warning-1: hsl(54, 38.46%, 94.9%);
  --color-warning-2: hsl(54.86, 42.17%, 83.73%);
  --color-warning-3: hsl(54.46, 46.1%, 72.35%);
  --color-warning-4: hsl(54.55, 50.25%, 61.37%);
  --color-warning-5: hsl(55.18, 53.73%, 50%);
  --color-warning-6: hsl(55.79, 57.58%, 38.82%);
  --color-warning-7: hsl(56.51, 61.43%, 27.45%);
  --color-warning-8: hsl(56.73, 66.27%, 16.27%);
  --color-warning-9: hsl(56.67, 69.23%, 5.1%);
  --color-danger-1: hsl(0, 52%, 95.1%);
  --color-danger-2: hsl(0, 51.81%, 83.73%);
  --color-danger-3: hsl(0, 51.43%, 72.55%);
  --color-danger-4: hsl(0, 52.28%, 61.37%);
  --color-danger-5: hsl(0, 52.16%, 50%);
  --color-danger-6: hsl(0, 52.28%, 38.63%);
  --color-danger-7: hsl(0, 51.43%, 27.45%);
  --color-danger-8: hsl(0, 51.81%, 16.27%);
  --color-danger-9: hsl(0, 52%, 4.9%);
  --color-info-1: hsl(187.06, 68%, 95.1%);
  --color-info-2: hsl(187.5, 68.29%, 83.92%);
  --color-info-3: hsl(187.5, 68.57%, 72.55%);
  --color-info-4: hsl(186.72, 67.68%, 61.18%);
  --color-info-5: hsl(186.94, 67.84%, 50%);
  --color-info-6: hsl(187.16, 67.68%, 38.82%);
  --color-info-7: hsl(187.5, 68.57%, 27.45%);
  --color-info-8: hsl(186.43, 68.29%, 16.08%);
  --color-info-9: hsl(187.06, 68%, 4.9%);
  --color-gray-1: hsl(60, 4%, 95.1%);
  --color-gray-2: hsl(60, 3.61%, 83.73%);
  --color-gray-3: hsl(60, 4.29%, 72.55%);
  --color-gray-4: hsl(60, 4.04%, 61.18%);
  --color-gray-5: hsl(60, 4.31%, 50%);
  --color-gray-6: hsl(60, 4.04%, 38.82%);
  --color-gray-7: hsl(60, 4.29%, 27.45%);
  --color-gray-8: hsl(60, 3.61%, 16.27%);
  --color-gray-9: hsl(60, 4%, 4.9%);

  /* Shadows */
  --shadow-1: 0 0.1rem 0.3rem hsla(0, 0%, 0%, 0.12),
    0 0.1rem 0.2rem hsla(0, 0%, 0%, 0.24);
  --shadow-2: 0 0.3rem 0.6rem hsla(0, 0%, 0%, 0.15),
    0 0.2rem 0.4rem hsla(0, 0%, 0%, 0.12);
  --shadow-3: 0 1rem 2rem hsla(0, 0%, 0%, 0.15),
    0 0.3rem 0.6rem hsla(0, 0%, 0%, 0.1);
  --shadow-4: 0 1.5rem 2.5rem hsla(0, 0%, 0%, 0.15),
    0 0.5rem 1rem hsla(0, 0%, 0%, 0.05);
  --shadow-5: 0 2rem 4rem hsla(0, 0%, 0%, 0.2);
}
